/* eslint-disable import/no-extraneous-dependencies */
import { css, html, LitElement } from 'lit';
import { property, query } from 'lit/decorators.js';

import { insertIed } from '@openenergytools/scl-lib';
import { newEditEvent } from '@openenergytools/open-scd-core';

/** An editor [[`plugin`]] to configure `Report`, `GOOSE`, `SampledValue` control blocks and its `DataSet` */
export default class ImportIEDPlugin extends LitElement {
  /** The document being edited as provided to plugins by [[`OpenSCD`]]. */
  @property({ attribute: false })
  doc!: XMLDocument;

  /** SCL change indicator */
  @property({ type: Number })
  docVersion = 0;

  @query('input') input!: HTMLInputElement;

  async run() {
    this.input.click();
  }

  /** Loads the file `event.target.files[0]` into [[`src`]] as a `blob:...`. */
  async importIED(event: Event): Promise<void> {
    const file = (<HTMLInputElement | null>event.target)?.files?.item(0);
    if (!file) return;

    const text = await file.text();
    const newIed = new DOMParser()
      .parseFromString(text, 'application/xml')
      .querySelector('IED');

    const scl = this.doc.querySelector('SCL');
    const isIedNameUnique = !scl?.querySelector(
      `IED[name="${newIed?.getAttribute('name')}"]`
    );

    if (scl && newIed && isIedNameUnique)
      this.dispatchEvent(newEditEvent(insertIed(scl, newIed),{title: `Import IED ${newIed.getAttribute('name')}`}));
  }

  render() {
    return html`<input
      @click=${({ target }: MouseEvent) => {
        // eslint-disable-next-line no-param-reassign
        (<HTMLInputElement>target).value = '';
      }}
      @change=${this.importIED}
      id="importied-plugin-input"
      accept=".iid,.cid,.icd"
      type="file"
    />`;
  }

  static styles = css`
    input {
      width: 0;
      height: 0;
      opacity: 0;
    }
  `;
}
